@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
*{
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: 'Muli', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
  background-color:white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.nav-link {
  font-weight: 500!important;
}

.logos{
  display: flex;
  align-items: center;
  justify-content: center;
}

.logos span{
  margin: 0 5px;
}

form{
  width: 600px;
  box-shadow: 0 0 5px rgba(0,0,0, 0.3);
  border-radius: 10px;
  padding: 10px;
}

form input{
  width: 100%;
  border: none;
  outline: none;
}

form textarea {
  width: 100%;
  height: 80px;
  line-height: 1.5;
  border: none;
  padding: 10px 0;
  resize: none;
  outline: none;
}
#edittitle{
  font-weight: bold;
}

.input{
  display: flex;
  justify-content: center;
  /* flex-direction: column; */
  align-items: center;
}

.addbtn{
  transform: translateY(30px);
  border-radius: 50% !important;
  float: right;
  font-weight: bold!important;
 
}

.notes{
  display: flex;
  flex-wrap: wrap;
 
}
.card{
  transition: 0.3s;
  border-radius: 10px !important;
}
.card:hover{
  box-shadow:0 0 20px rgba(0,0,0, 0.3) ;
  transform: scale(1.05);
}

#editor{
  
display: none;
width: 100%;
transition: 0.4s ;
}
.editform{
  transform: translateY(-400px);
  z-index: 10;
  max-width: 90%;
  display: block;
  margin: 0 auto;
  background-color: white;
  height: 200px;
  
}



@media (max-width: 576px) { 
  .notes{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .card{
    width: 100% !important;
  }
 }
 @media (max-width: 768px) { 
  .notes{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .card{
    width: 100% !important;
  }
 }


@media (max-width: 1202px) { 
  .notes{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
 }



